module.exports = "<!-- /**\n* @Author: Phillip Curl <philcurl>\n* @Date:   2016-04-18T09:01:46-04:00\n* @Email:  phillipcurl@gmail.com\n* @Project: Libations-Portal\n* @Last modified by:   philcurl\n* @Last modified time: 2016-04-23T23:18:51-04:00\n*/ -->\n\n<div class=\"list-group-item\">\n  <h5><small class=\"text-muted\">3/4</small> Event Feedback</h5>\n</div>\n<div class=\"list-group-item\">\n  <div class=\"row\">\n    <div class=\"col-sm-6\">\n      <div class=\"form-group\">\n        <label for=\"\">Mail-In Rebates Distrubuted</label>\n        <div class=\"input-group\">\n          <span class=\"input-group-addon\">#</span>\n          <input [(ngModel)]=\"rebates\" (ngModelChange)=\"recapState.setState('isDirty', true)\" class=\"form-control\" placeholder=\"5\">\n        </div>\n      </div>\n    </div>\n    <div class=\"col-sm-6\">\n      <div class=\"form-group\">\n        <label for=\"\">Percentage Who Liked Sample</label>\n        <div class=\"input-group\">\n          <input [(ngModel)]=\"percentLikes\" (ngModelChange)=\"recapState.setState('isDirty', true)\" class=\"form-control\" placeholder=\"60\">\n          <span class=\"input-group-addon\">%</span>\n        </div>\n      </div>\n    </div>\n    <div class=\"col-sm-12\">\n      <div class=\"form-group\">\n        <label for=\"\">Is there a display or rack in the store?</label>\n        <textarea [(ngModel)]=\"display\" (ngModelChange)=\"recapState.setState('isDirty', true)\" class=\"form-control\" id=\"\" rows=\"2\" placeholder=\"Yes, display near check out area of wine section complete with an animatronic cardboard woman talking about the product.\"></textarea>\n      </div>\n    </div>\n  </div>\n  <form (submit)=\"addComment()\">\n    <div class=\"form-group\">\n      <label for=\"\">Top Five Favorable Comments</label>\n      <div *ngIf=\"userComments.length < 5\" class=\"input-group\">\n        <input [(ngModel)]=\"newComment\" (ngModelChange)=\"recapState.setState('isDirty', true)\" class=\"form-control\" placeholder=\"The Coconut is so refreshing!\">\n        <span class=\"input-group-btn\">\n          <button class=\"btn btn-secondary\" type=\"submit\">Add</button>\n        </span>\n      </div>\n    </div>\n  </form>\n  <blockquote *ngFor=\"let comment of userComments\" class=\"blockquote\">\n    <p class=\"m-b-0\">{{comment}}</p>\n  </blockquote>\n  <div class=\"form-group\">\n    <label for=\"exampleTextarea\">Additional Feedback</label>\n    <textarea class=\"form-control\" id=\"exampleTextarea\" rows=\"3\"></textarea>\n  </div>\n</div>\n<div class=\"list-group-item\">\n  <button (click)=\"showPrevious()\" type=\"button\" class=\"btn btn-secondary\">Go Back</button>\n  <button *ngIf=\"userComments.length >= 5\"  (click)=\"showNext()\" href=\"#recapHeader\" type=\"button\" class=\"btn btn-primary\">Next</button>\n</div>\n"

