module.exports = "<!-- /**\n* @Author: Phillip Curl <philcurl>\n* @Date:   2016-04-18T09:01:46-04:00\n* @Email:  phillipcurl@gmail.com\n* @Project: Libations-Portal\n* @Last modified by:   philcurl\n* @Last modified time: 2016-04-23T23:18:51-04:00\n*/ -->\n\n<div class=\"list-group-item\">\n  <h5><small class=\"text-muted\">4/4</small> Recap Summary</h5>\n</div>\n<div class=\"list-group-item p-y-1\">\n  <h6>Event Info</h6>\n  <hr />\n  <div class=\"row\">\n    <div class=\"col-sm-6\">\n      <div class=\"form-group row\">\n        <label class=\"col-md-3 form-control-label\">Name</label>\n        <div class=\"col-md-9\">\n          <p class=\"form-control-static\">{{recapState.recap.employeeName}}</p>\n        </div>\n      </div>\n      <div class=\"form-group row\">\n        <label class=\"col-md-3 form-control-label\">Date</label>\n        <div class=\"col-md-9\">\n          <p class=\"form-control-static\">{{recapState.recap.recapDate}}</p>\n        </div>\n      </div>\n      <div class=\"form-group row\">\n        <label class=\"col-md-3 form-control-label\">Hours</label>\n        <div class=\"col-md-9\">\n          <p class=\"form-control-static\">{{recapState.recap.employeeHours}}</p>\n        </div>\n      </div>\n      <div class=\"form-group row\">\n        <label class=\"col-md-3 form-control-label\">Demo Company</label>\n        <div class=\"col-md-9\">\n          <p class=\"form-control-static\">{{recapState.recap.demoCompany}}</p>\n        </div>\n      </div>\n      <div class=\"form-group row\">\n        <label class=\"col-md-3 form-control-label\">Distributor</label>\n        <div class=\"col-md-9\">\n          <p class=\"form-control-static\">{{recapState.recap.distributor}}</p>\n        </div>\n      </div>\n    </div>\n    <div class=\"col-sm-6\">\n      <div class=\"form-group row\">\n        <label class=\"col-md-3 form-control-label\">Store Number</label>\n        <div class=\"col-md-9\">\n          <p class=\"form-control-static\">{{recapState.recap.storeNum}}</p>\n        </div>\n      </div>\n      <div class=\"form-group row\">\n        <label class=\"col-md-3 form-control-label\">Store Address</label>\n        <div class=\"col-md-9\">\n          <p class=\"form-control-static\">{{recapState.recap.storeAddress}}</p>\n        </div>\n      </div>\n      <div class=\"form-group row\">\n        <label class=\"col-md-3 form-control-label\">Store Contact</label>\n        <div class=\"col-md-9\">\n          <p class=\"form-control-static\">{{recapState.recap.storeContact}}</p>\n        </div>\n      </div>\n      <div class=\"form-group row\">\n        <label class=\"col-md-3 form-control-label\">Demo Location</label>\n        <div class=\"col-md-9\">\n          <p class=\"form-control-static\">{{recapState.recap.demoLocation}}</p>\n        </div>\n      </div>\n    </div>\n  </div>\n  <h6>Products</h6>\n  <hr />\n  <div class=\"row\">\n    <div class=\"col-sm-12 table-responsive\">\n      <table class=\"table table-striped table-hover table-sm\">\n        <thead>\n          <tr>\n            <th>Product Demonstrated</th>\n            <th># of Samples Poured</th>\n            <th># of Bottles Sold</th>\n            <th>Regular Price</th>\n            <th>Demo Price</th>\n          </tr>\n        </thead>\n        <tbody>\n          <tr *ngFor=\"let product of recapState.recap.products; let idx = index\">\n            <td>{{product.name}}</td>\n            <td>{{product.samplesPoured}}</td>\n            <td>{{product.bottlesSold}}</td>\n            <td>{{product.regularPrice | currency:'USD':true:'2.2-2'}}</td>\n            <td>{{product.demoPrice | currency:'USD':true:'2.2-2'}}</td>\n          </tr>\n        </tbody>\n      </table>\n    </div>\n  </div>\n  <h6>Event Feedback</h6>\n  <hr />\n  <div class=\"row\">\n    <div class=\"col-sm-12\">\n      <blockquote *ngFor=\"let comment of recapState.recap.comments\" class=\"blockquote\">\n        <p class=\"m-b-0\"><small>{{comment}}</small></p>\n      </blockquote>\n    </div>\n  </div>\n\n  <!-- <pre>this.recapState.state.recap = {{ recapState.state.recap | json }}</pre> -->\n</div>\n<div class=\"list-group-item\">\n  <button (click)=\"showPrevious()\" type=\"button\" class=\"btn btn-secondary\">Go Back</button>\n  <button (click)=\"showNext()\" type=\"button\" class=\"btn btn-primary\">Submit</button>\n</div>\n"

