module.exports = "<app-card title=\"{{event.title}}\" cardClass=\"with-scroll\">\n  <h6 class=\"card-subtitle text-muted\">\n    <i class=\"fa fa-calendar-o fa-fw text-primary\"></i>\n    {{event.date}}\n  </h6>\n  <div class=\"list-group list-group-flush\">\n    <div class=\"list-group-item\">\n      <p class=\"card-text\">\n        {{event.description}}\n      </p>\n      <p *ngIf=\"isDetail\" class=\"card-text\">\n        <strong>Event Contact:</strong> {{event.contactFName}} {{event.contactLName}}\n      </p>\n    </div>\n    <a *ngIf=\"isDetail\" href=\"tel:+{{event.phoneNumber}}\" class=\"list-group-item\">\n      <i class=\"fa fa-phone fa-fw text-primary\"></i>\n      {{event.phoneNumber}}\n    </a>\n    <div *ngIf=\"isDetail\" class=\"list-group-item\">\n      <i class=\"fa fa-map-marker fa-fw text-primary\"></i>\n      {{event.address.streetAddress}} {{event.address.city}}, {{event.address.state}} {{event.address.zip}}\n    </div>\n    <a *ngIf=\"isDetail\"\n       href=\"http://maps.google.com/?q={{event.address.streetAddress}} {{event.address.city}}, {{event.address.state}} {{event.address.zip}}\"\n       class=\"list-group-item\"\n       target=\"_blank\">\n      <img src=\"http://maps.googleapis.com/maps/api/staticmap?center={{event.address.state}}&size=640x300&&scale=2&format=jpg&zoom=15&maptype=roadmap&markers=color:blue%7Clabel:S%7C11211%7C11206%7C11222&key=AIzaSyCNDwPJWHjhxWPFpbFdSo8KB5hGj3q0wKA\" class=\"img-fluid\" />\n    </a>\n    <a *ngIf=\"!isDetail\" href=\"http://maps.google.com/?q={{event.address.streetAddress}}+{{event.address.city}}+{{event.address.state}}+{{event.address.zip}}\" target=\"_blank\" class=\"list-group-item\">\n      <i class=\"fa fa-map-marker fa-fw\"></i>\n      {{event.address.streetAddress}} {{event.address.city}}, {{event.address.state}} {{event.address.zip}}\n    </a>\n    <div class=\"list-group-item\">\n      <span class=\"addtocalendar atc-style-blue\">\n        <var class=\"atc_event\">\n            <var class=\"atc_date_start\">2016-05-04 12:00:00</var>\n            <var class=\"atc_date_end\">2016-05-04 18:00:00</var>\n            <var class=\"atc_timezone\">Europe/London</var>\n            <var class=\"atc_title\">Star Wars Day Party</var>\n            <var class=\"atc_description\">May the force be with you</var>\n            <var class=\"atc_location\">Tatooine</var>\n            <var class=\"atc_organizer\">Luke Skywalker</var>\n            <var class=\"atc_organizer_email\">luke@starwars.com</var>\n        </var>\n    </span>\n    </div>\n    <a *ngIf=\"!isDetail\" [routerLink]=\"['./', event.id]\" class=\"list-group-item\">\n      <i class=\"fa fa-arrow-right fa-fw\"></i>\n      View Event\n    </a>\n    <a [routerLink]=\"['../recap', event.id]\" class=\"list-group-item\" [class.list-group-item-success]=\"isDetail\">\n      <i class=\"fa fa-plus fa-fw\"></i>\n      Add a Recap\n    </a>\n  </div>\n</app-card>\n\n\n<!--<div class=\"card\">\n  <div class=\"card-block\">\n    <h4 class=\"card-title\"><small>{{event.title}}</small></h4>\n    <h6 class=\"card-subtitle text-muted\">\n      <i class=\"fa fa-calendar-o fa-fw text-primary\"></i>\n      {{event.date}}\n    </h6>\n  </div>\n  <div class=\"list-group list-group-flush\">\n    <div class=\"list-group-item\">\n      <p class=\"card-text\">\n        {{event.description}}\n      </p>\n      <p *ngIf=\"isDetail\" class=\"card-text\">\n        <strong>Event Contact:</strong> {{event.contactFName}} {{event.contactLName}}\n      </p>\n    </div>\n    <a *ngIf=\"isDetail\" href=\"tel:+{{event.phoneNumber}}\" class=\"list-group-item\">\n      <i class=\"fa fa-phone fa-fw text-primary\"></i>\n      {{event.phoneNumber}}\n    </a>\n    <div *ngIf=\"isDetail\" class=\"list-group-item\">\n      <i class=\"fa fa-map-marker fa-fw text-primary\"></i>\n      {{event.address.streetAddress}} {{event.address.city}}, {{event.address.state}} {{event.address.zip}}\n    </div>\n    <a *ngIf=\"isDetail\"\n       href=\"http://maps.google.com/?q={{event.address.streetAddress}} {{event.address.city}}, {{event.address.state}} {{event.address.zip}}\"\n       class=\"list-group-item\"\n       target=\"_blank\">\n      <img src=\"http://maps.googleapis.com/maps/api/staticmap?center={{event.address.state}}&size=640x300&&scale=2&format=jpg&zoom=15&maptype=roadmap&markers=color:blue%7Clabel:S%7C11211%7C11206%7C11222&key=AIzaSyCNDwPJWHjhxWPFpbFdSo8KB5hGj3q0wKA\" class=\"img-fluid\" />\n    </a>\n    <a *ngIf=\"!isDetail\" href=\"http://maps.google.com/?q={{event.address.streetAddress}}+{{event.address.city}}+{{event.address.state}}+{{event.address.zip}}\" target=\"_blank\" class=\"list-group-item\">\n      <i class=\"fa fa-map-marker fa-fw\"></i>\n      {{event.address.streetAddress}} {{event.address.city}}, {{event.address.state}} {{event.address.zip}}\n    </a>\n    <div class=\"list-group-item\">\n      <div title=\"Add to Calendar\" class=\"addeventatc\">\n        Add to Calendar\n        <span class=\"start\">05/05/2016 08:00 AM</span>\n        <span class=\"end\">05/05/2017 10:00 AM</span>\n        <span class=\"timezone\">America/New_York</span>\n        <span class=\"title\">Summary of the event</span>\n        <span class=\"description\">Description of the event</span>\n        <span class=\"location\">Location of the event</span>\n        <span class=\"date_format\">MM/DD/YYYY</span>\n      </div>\n    </div>\n    <a *ngIf=\"!isDetail\" [routerLink]=\"['./', event.id]\" class=\"list-group-item\">\n      <i class=\"fa fa-arrow-right fa-fw\"></i>\n      View Event\n    </a>\n    <a [routerLink]=\"['../recap', event.id]\" class=\"list-group-item\" [class.list-group-item-success]=\"isDetail\">\n      <i class=\"fa fa-plus fa-fw\"></i>\n      Add a Recap\n    </a>\n  </div>\n</div>-->\n"

